
import React, {useEffect, useState} from 'react';
import {useNavigate} from "react-router-dom";
import 'bootstrap-icons/font/bootstrap-icons.css';
import "../../config/index.css";
import holidayListData from "../../utils/holidayListData";
import {useWorkData} from "../../utils/WorkData";
const WorkScheduleList = () =>  {

        const today = new Date();
        const [year, setYear] = useState(today.getFullYear());
        const [month, setMonth] = useState(today.getMonth() + 1);
        const [schedule, setSchedule] = useState([]);
        const navigate = useNavigate();
        const {error, loading, workDataList} = useWorkData(year, month);
    // const dummyData = {
    //     "2025-01-01": { attendanceType: "휴일", workType: "", checkInTime: "", checkOutTime: "", memo: "공휴일" },
    //     "2025-01-02": { attendanceType: "출근", workType: "본사", checkInTime: "09:00", checkOutTime: "18:00", memo: "업무 교육" },
    //     "2025-01-03": { attendanceType: "휴가", workType: "", checkInTime: "", checkOutTime: "", memo: "연차 사용" },
    //     "2025-01-06": { attendanceType: "출근", workType: "현장", checkInTime: "09:00", checkOutTime: "18:00", memo: "" },
    //     "2025-02-03": { attendanceType: "출근", workType: "현장", checkInTime: "09:00", checkOutTime: "18:00", memo: "" },
    // };

    useEffect(() => {
        const fetchSchedule = async () => {
            try{
                const holidayData = await holidayListData(); // 공휴일 데이터 가져오기
                const daysInMonth = new Date(year, month, 0).getDate();
                const newSchedule = Array.from({ length: daysInMonth }, (_, i) => {
                    const day = i + 1;
                    const key = `${year}-${String(month).padStart(2, '0')}-${String(day).padStart(2, '0')}`; // 날짜 형식 통일 (YYYY-MM-DD)
                    const weekday = new Date(year, month - 1, day).toLocaleDateString("ja-JP", { weekday: "short" });
                    const isWeekend = weekday === "土" || weekday === "日"; // 주말 확인
                    const HolidayType = holidayData[key]  // 휴일 확인 (값이 존재하는지 체크)
                    return {
                        date: `${day}日`,
                        weekday,
                        HolidayType,
                        isHoliday: workDataList[key]?.isHoliday || (HolidayType ? "휴일" : isWeekend ? "주말" : ""),
                        checkInTime: workDataList[key]?.checkInTime || "",
                        checkOutTime: workDataList[key]?.checkOutTime || "",
                        checkOutDate: workDataList[key]?.checkOutDate || "",
                        memo: workDataList[key]?.memo || (HolidayType ? holidayData[key] : isWeekend ? "주말" : ""),
                        workType: workDataList[key]?.workType || "",
                        workLocation: workDataList[key]?.workLocation || "",
                        breakTimeIn: workDataList[key]?.breakTimeIn || "",
                        breakTimeOut: workDataList[key]?.breakTimeOut || "",
                        styleClass: HolidayType ? "holiday" : isWeekend ? "weekend" : "",
                    };
                });
                setSchedule(newSchedule);
            }catch (error){
                console.error("근무 데이터를 불러오는데 실패했습니다.", error);
            }
        };
        fetchSchedule();
    }, [year, month]);

        const handleInputChange = (index, field, value) => {
            const newSchedule = [...schedule];
            newSchedule[index][field] = value;
            setSchedule(newSchedule);
        };

    const changeMonth = (direction) => {
        setMonth((prev) => {
            let newMonth = prev + direction;
            let newYear = year;

            if (newMonth < 1) {
                newYear -= 1;
                newMonth = 12;
            } else if (newMonth > 12) {
                newYear += 1;
                newMonth = 1;
            }

            setYear(newYear);
            return newMonth;
        });
    };

    const handleClickMypage = () => {
        const scheduleData  = {stateInfo : "true"};
        navigate("/workSchedule/dashBoard", {state: scheduleData });
    }

    const handleClickEdit = (Id) => {
        navigate(`/workSchedule/detail/${Id}`);
    }

    if (loading) return <p>Loading...</p>;
    if (error) return <p>{error}</p>;

        return (
            <div className="container">
                <h3 className="text-dark mb-1">WORK SCHEDULE</h3>
                <div className="d-flex justify-content-center align-items-center">
                    <button onClick={() => changeMonth(-1)}>◀</button>
                    <h2 className="px-3">{year} / {String(month).padStart(2, "0")}</h2>
                    <button onClick={() => changeMonth(1)}>▶</button>
                </div>
                <button type="button" className="action-button" onClick={handleClickMypage}>
                    기본 정보 수정
                </button>
                <div className="table-responsive">
                    <table className="table table-striped">
                        <thead>
                        <tr>
                            <th className="text-center">日付</th>
                            <th className="text-center">曜日</th>
                            <th className="text-center">休暇・勤怠</th>
                            <th className="text-center">区分</th>
                            <th className="text-center">開始時間</th>
                            <th className="text-center">終了時間</th>
                            <th className="text-center">비고</th>
                        </tr>
                        </thead>
                        <tbody>
                        {schedule.map((day, index) => (
                            <tr key={index} >
                                <td className={day.styleClass}>
                                    {day.date} &nbsp;
                                        <i className="bi bi-pencil-fill"
                                           onClick={() => handleClickEdit(day.date || "")}
                                        >
                                        </i>
                                </td>
                                <td className={day.styleClass}>{day.weekday}</td>
                                <td className={day.styleClass}>{day.isHoliday}</td>
                                <td className={day.styleClass}>{day.workType} </td>
                                <td className={day.styleClass}>{day.checkInTime} </td>
                                <td className={day.styleClass}>{day.checkOutTime} </td>
                                <td className={day.styleClass}>{day.memo} </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
        );
}
export default WorkScheduleList;